import loadErr from '@/components/loadErr.vue'

export default {
	data() {
		return {
			imgArr: [], //图片数组
			isLoadErr: false
		}
	},
	components: { loadErr },
	onLoad() {
		this.getImgsArr()
	},
	methods: {
		toPreviewImg(idx) {
			const $this = this
			uni.previewImage({
				current: $this.imgArr[idx],
				urls: $this.imgArr
			})
		},
		toRelaodData() {
			this.imgArr = []
			this.isLoadErr = false
			this.getImgsArr()
		},
		getImgsArr() {
			uni.showLoading({
				title: '数据加载中',
				mask: true
			})
			this.$api.ajax({
				url: this.$config.getImgs
			})
			.then((res) => {
				if(res.statusCode == 200 && res.data.code == 0) {
					let arr = res.data.data
					this.imgArr = arr
					setTimeout(() => {
						uni.hideLoading();
					}, 200)
				}else {
					this.isLoadErr = true
					this.$api.warnNotice(res.data.message)
				}
			})
			.catch(() => {
				this.isLoadErr = true
				this.$api.warnNotice('照片加载失败')
			})
		}
	}
}
